<template>
  <!-- 导航栏 -->
  <div class="parklot-container">
    <van-nav-bar title="黄金商业广场停车场" left-text="返回" left-arrow fixed @click-left="$router.back()">
      <template #right>
        <van-icon name="search" size="18" />
      </template>
    </van-nav-bar>
    <!-- /导航栏 -->
    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000">
      <van-swipe-item v-for="(image, index) in parklotImages" :key="index">
        <img v-lazy="image" />
      </van-swipe-item>
    </van-swipe>
    <!-- 停车场详细信息 -->
    <van-tabs v-model="activeName" color="skyblue">
      <van-tab title="详细情况" name="a" class="content">
        <p>
          黄金商业广场为保证顾客有舒适、贴心的购物环境，商场公共区域已成精装修格局。全场WIFI免费覆盖，集中有效的媒体宣传活动，以此带动整个商场的正常运营，让商户在短期内进入稳定的盈利中。
        </p>
        <p>
          黄金商业广场专业的运营团队为您的经营提供专业的服务，全场统一收银、提供开业指导，货源优化、广告投放等超出您期待的服务。商户们只需要以专业、热情、周到的服务来迎接每一位顾客的到来。
        </p>
        <van-image width="100%" height="6rem" fit="contain" :src="parklotImages[0]" />
        <p>
          黄金商业广场停车场，隶属黄金商业广场内部停车场，是大良城区新桂区域中心商业地带，是大良中心繁华地带人气较高的停车场。
        </p>
      </van-tab>
      <van-tab title="收费规则" name="b" class="content">
        <p>
          小型车——白天时段（8:00——20:00)2小时内5元/车，2小时后每30分钟1元/车，夜间时段（20:00—8:00）4元/车。
        </p>
        <p>
          中型车——白天时段（8:00——20:00)2小时内8元/车，2小时后每30分钟2元/车，夜间时段（20:00—8:00)6元/车。
        </p>
        <p>
          大型车——白天时段（8:00——20:00)2小时内10元/车,2小时后每30分钟3元/车,夜间时段（20:00—8:00）8元/车。
        </p>
      </van-tab>
      <van-tab title="附近周边" name="c" class="content">
        <van-image width="100%" height="100%" fit="contain" :src="mapImages" />
        <van-row>
          <van-col span="4">超市：</van-col>
          <van-col span="20">百惠，沃尔玛</van-col>
        </van-row>
        <van-row>
          <van-col span="4">餐饮：</van-col>
          <van-col span="20">星巴克，顺德人家，港式茶餐厅，大岛站居酒屋，九哥龙虾馆等</van-col>
        </van-row>
        <van-row>
          <van-col span="4">住宿：</van-col>
          <van-col span="20">Eleven-迷迭香复古式公寓，麦芽糖榻榻米</van-col>
        </van-row>
      </van-tab>
    </van-tabs>
    <!-- /停车场详细信息 -->

    <!-- 预约停车 -->
    <van-sku v-model="show" :sku="sku" :goods="goods" :goods-id="goodsId" :quota="quota" :quota-used="quotaUsed"
      :hide-stock="sku.hide_stock" show-add-cart-btn reset-stepper-on-hide :initial-sku="initialSku" @buy-clicked="onBuyClicked">

      <!-- 自定义 sku-header-price -->
      <template #sku-header="props">
        <div class="van-sku-header van-hairline--bottom">
          <div class="van-sku-header__img-wrap">
            <img :src="parklotImages[1]" />
          </div>
          <div class="van-sku-header__goods-info">
            <div class="van-sku__goods-price">
              <span class="van-sku__price-symbol">￥</span><span class="van-sku__price-num">5.00</span>
            </div>
            <div class="van-sku-header-item">
              <span class="van-sku__stock">剩余</span><span class="van-sku__stock-num van-sku__stock-num--highlight">6{{ props.stock_num }}</span>&nbsp;个车位
            </div>
          </div>
          <div></div>
        </div>
      </template>

      <!-- 自定义 步进器 -->
      <template #sku-stepper="props">
        <div class="van-sku__stepper">
          {{ props.stepper }}
        </div>
      </template>

      <!-- 自定义 sku actions -->
      <template #sku-actions="props">
        <div class="van-sku-actions">

          <!-- 直接触发 sku 内部事件，通过内部事件执行 onBuyClicked 回调 -->
          <van-button square size="large" type="warning" @click="props.skuEventBus.$emit('sku:buy')">
            预定车位
          </van-button>
        </div>
      </template>
    </van-sku>
    <!-- /预约停车 -->
    <!-- 停车场列表 -->
    <van-divider>为你推荐</van-divider>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <van-card tag="推荐" price="4.00" desc="距您790m,步行需要9分钟" title="西山上筑停车场" :thumb="parklotImages[1]">
        <template #tags>
          <van-tag plain type="warning ">价格最低</van-tag>
          <van-tag plain type="warning ">一小时内免费</van-tag>
        </template>
      </van-card>
      <van-card price="5.00" desc="距您1.6km,驾车需要7分钟" title="明日广场露天停车场" :thumb="parklotImages[2]">
      </van-card>
      <van-card price="5.00" desc="距您2.0km,驾车需要9分钟" title="永旺购物中心停车场" :thumb="parklotImages[3]">
        <template #tags>
          <van-tag plain type="warning ">剩余车位多</van-tag>
        </template>
      </van-card>
      <van-card price="8.00" desc="距您2.4km,驾车需要10分钟" title="步行街停车场" :thumb="parklotImages[4]">
        <template #tags>
          <van-tag plain type="warning ">休闲娱乐</van-tag>
        </template>
      </van-card>
      <van-card price="5.00" desc="距您2.0km,驾车需要9分钟" title="永旺购物中心停车场" :thumb="parklotImages[3]">
        <template #tags>
          <van-tag plain type="warning ">剩余车位多</van-tag>
        </template>
      </van-card>
      <van-card price="8.00" desc="距您2.4km,驾车需要10分钟" title="步行街停车场" :thumb="parklotImages[4]">
        <template #tags>
          <van-tag plain type="warning ">休闲娱乐</van-tag>
        </template>
      </van-card>
      <van-card price="5.00" desc="距您2.0km,驾车需要9分钟" title="永旺购物中心停车场" :thumb="parklotImages[3]">
        <template #tags>
          <van-tag plain type="warning ">剩余车位多</van-tag>
        </template>
      </van-card>
      <van-card price="8.00" desc="距您2.4km,驾车需要10分钟" title="步行街停车场" :thumb="parklotImages[4]">
        <template #tags>
          <van-tag plain type="warning ">休闲娱乐</van-tag>
        </template>
      </van-card>
    </van-list>
    <!-- /停车场列表 -->
    <van-divider>没有更多了</van-divider>
    <!-- 底部导航 -->
    <van-goods-action>
      <van-goods-action-button color="#be99ff" type="warning" @click="onReserve" text="预约停车" />
      <van-goods-action-button color="#7232dd" type="danger" text="立即停车" />
    </van-goods-action>
    <!-- /底部导航 -->
  </div>
</template>

<script>
  export default {
    name: 'ParklotIndex',
    components: {},
    props: {},
    data() {
      return {
        activeName: 'a',
        show: false,
        loading: false,
        finished: true,
        parklotImages: [
          require('./assets/parklot1.jpg'),
          require('./assets/parklot2.jpg'),
          require('./assets/parklot3.jpg'),
          require('./assets/parklot4.jpg'),
          require('./assets/parklot5.jpg')
        ],
        mapImages: require('./assets/map.png'),
        goodsId: '123',
        quota: 0,
        quotaUsed: 0,
        initialSku: {
          // 键：skuKeyStr（sku 组合列表中当前类目对应的 key 值）
          // 值：skuValueId（规格值 id）
          s1: '1',
          s2: '1',
          // 初始选中数量
          selectedNum: 3,
          // 初始选中的商品属性
          // 键：属性id
          // 值：属性值id列表
          selectedProp: {
            123: [1222]
          }
        },
        sku: {
          // 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
          // 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
          tree: [{
            k: 'A区可选车位', // skuKeyName：规格类目名称
            k_s: 's1', // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
            v: [{
                id: '1', // skuValueId：规格值 id
                name: '1001' // skuValueName：规格值名称
              },
              {
                id: '1',
                name: '1002'
              },
              {
                id: '3',
                name: '1003'
              },
              {
                id: '1',
                name: '1004'
              },
              {
                id: '5',
                name: '1005'
              },
              {
                id: '6',
                name: '1006'
              },
              {
                id: '7',
                name: '1007'
              },
              {
                id: '1',
                name: '1008'
              },
              {
                id: '9',
                name: '1009'
              },
              {
                id: '1',
                name: '1010'
              },
              {
                id: '11',
                name: '1011'
              },
              {
                id: '1',
                name: '1012'
              }
            ]
          }],
          // 所有 sku 的组合列表，比如红色、M 码为一个 sku 组合，红色、S 码为另一个组合
          list: [{
            id: 2259, // skuId
            s1: '1', // 规格类目 k_s 为 s1 的对应规格值 id
            s2: '1', // 规格类目 k_s 为 s2 的对应规格值 id
            price: 100, // 价格（单位分）
            stock_num: 10 // 当前 sku 组合对应的库存
          }],
          price: '5.00', // 默认价格（单位元）
          stock_num: 12, // 商品总库存
          collection_id: 2261, // 无规格商品 skuId 取 collection_id，否则取所选 sku 组合对应的 id
          none_sku: false, // 是否无规格商品
          messages: [{
              // 预约车牌
              datetime: '0',
              multiple: '0',
              name: '车牌',
              type: 'text',
              required: '1',
              placeholder: '请输入车牌号'
            },
            {
              // 预约时间
              datetime: '1', // 留言类型为 time 时，是否含日期。'1' 表示包含
              multiple: '0', // 留言类型为 text 时，是否多行文本。'1' 表示多行
              name: '预约时间', // 留言名称
              type: 'date', // 留言类型，可选: id_no（身份证）, text, tel, date, time, email
              required: '1', // 是否必填 '1' 表示必填
              placeholder: '请选择预约时间' // 可选值，占位文本
            },
            {
              // 预停时长
              datetime: '1', // 留言类型为 time 时，是否含日期。'1' 表示包含
              multiple: '0', // 留言类型为 text 时，是否多行文本。'1' 表示多行
              name: '预停时长', // 留言名称
              type: 'time', // 留言类型，可选: id_no（身份证）, text, tel, date, time, email
              required: '1', // 是否必填 '1' 表示必填
              placeholder: '请选择预停时长' // 可选值，占位文本
            },
            {
              // 商品留言
              datetime: '0', // 留言类型为 time 时，是否含日期。'1' 表示包含
              multiple: '0', // 留言类型为 text 时，是否多行文本。'1' 表示多行
              name: '留言', // 留言名称
              type: 'text', // 留言类型，可选: id_no（身份证）, text, tel, date, time, email
              required: '0', // 是否必填 '1' 表示必填
              placeholder: '' // 可选值，占位文本
            }
          ],
          hide_stock: false // 是否隐藏剩余库存
        },
        goods: {
          // 默认商品 sku 缩略图
          picture: 'https://img.yzcdn.cn/1.jpg'
        },
        messageConfig: {
          // 图片上传回调，需要返回一个promise，promise正确执行的结果需要是一个图片url
          uploadImg: () => {
            return new Promise((resolve) => {
              setTimeout(() => resolve(
                  'https://img.yzcdn.cn/upload_files/2017/02/21/FjKTOxjVgnUuPmHJRdunvYky9OHP.jpg!100x100.jpg'),
                1000)
            })
          },
          // 最大上传体积 (MB)
          uploadMaxSize: 3,
          // placeholder 配置
          placeholderMap: {
            text: 'xxx',
            tel: 'xxx'
          },
          // 初始留言信息
          // 键：留言 name
          // 值：留言内容
          initialMessages: {
            留言: '留言信息'
          }
        }
      }
    },
    computed: {},
    watch: {},
    created() {},
    mounted() {},
    methods: {
      onLoad() {},
      onReserve() {
        this.show = true
      },
      onBuyClicked() {

      }
    }
  }
</script>

<style scoped lang="less">
  .my-swipe {
    width: 100vw;
    height: 200px;

    /deep/.van-swipe-item img {
      max-width: 100%;
    }
  }

  .content {
    padding: 0 1.5em 0 1.5em;
    color: #767778;
    font-size: 0.6em;
    line-height: 2em;

    p {
      text-indent: 2em;
    }
  }
</style>
